<template>
  <div class="login_container">
    <div class="login_box">
      <div>
        <h3>CCview 系统</h3>
      </div>
      <el-form
        ref="login_from_Ref"
        :model="loginfrom"
        :rules="from_rules"
        label-width="0px"
        class="login_form"
      >
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input v-model="loginfrom.username" prefix-icon="iconfont icon-yonghu"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input
            v-model="loginfrom.password"
            prefix-icon="iconfont icon-suo"
            show-password
            @keyup.enter.native="login()"
          ></el-input>
          <!-- keyup.enter -->
        </el-form-item>
        <!-- 按钮 -->
        <el-form-item class="login_from_btn">
          <el-button class="button" type="primary" @click="login" plain>登录</el-button>
          <el-button type="info" @click="resetfrom" plain>重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginfrom: { username: '', password: '' },
      // 表单验证
      from_rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 5, max: 16, message: '长度在 6 到 16 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 重置
    resetfrom() {
      this.$refs.login_from_Ref.resetFields()
    },
    // 登录
    login() {
      this.$refs.login_from_Ref.validate(valid => {
        if (!valid) {
          return
        }
        this.$axios({
          method: 'post',
          url: '/login',
          data: this.loginfrom
        })
          .then(res => {
            if (res.data.islogin) {
              this.$message.success('登录成功')
              window.sessionStorage.setItem('token', JSON.stringify(res.data.userinfo))
              this.$router.push('/home')
            } else {
              this.$message.error('登录失败')
            }
          })
          .catch(err => {
            console.dir(err)
            this.$message.error('登录失败')
            // this.$options.methods.resetfrom()
          })
      })
    }
  }
}
</script>
<style lang="scss">
.el-form-item__error {
  color: #f8f8f9;
  font-size: 14px;
}
</style>

<style scoped lang="scss">
.login_container {
  height: 100%;
  background-size: cover;
  background-image: url('../assets/images/backgroundImage.jpg');
  background-repeat: no-repeat;
}
.login_box {
  width: 400px;
  height: 260px;
  background-color: #ffffff59;

  position: absolute;
  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.login_from_btn {
  display: flex;
  justify-content: flex-end;
}

h3 {
  color: #fff;
  text-align: center;
}
</style>
